import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store ({
    state: {
        expenseToCopy: {},
    },
    getters: {
        getExpenseToCopy(state) {
            return state.expenseToCopy;
        },
    },
    mutations: {
        setExpenseToCopy(state, payload) {
            state.expenseToCopy=payload;
        },
    },
    actions: {
        setExpenseToCopy({commit}, users) {
            commit('setExpenseToCopy', users);
        },
    }
});