





























import { Component, Prop, Vue } from 'vue-property-decorator';
import firebase from 'firebase';
require('firebase/database');

type Expense = {
  user:string,
  category:string,
  description:string,
  date:string,
  amount: string,
  id: string,
}

@Component
export default class Expenses extends Vue {
  private filters: Array<string> = ["Żywność","Kredyt","Środki czystości", "Zwierzęta", "Rozrywka", "Rachunki", "Wyposażenie", "Inne"]
  private months: Array<string> = ["Styczeń", "Luty", "Marzec", "Kwiecień", "Maj", "Czerwiec", "Lipiec", "Sierpień", "Wrzesień", "Październik", "Listopad", "Grudzień"]
  private selectedFilter :string = ""
  private allSelectedFilters :Array<string> = []
  private selected :string = ""
  private users :Array<string> = []
  private selectedExpenses :Array<Expense> = []
  private expenses :Array<Expense> = []
  private fields :Array<any> = [
    {
      key: 'selectedExpenses',
      label: ' '
    },
    {
      key: 'category',
      sortable: true,
      label: 'Kategoria'
    },
    {
      key: 'description',
      sortable: true,
      label: 'Opis'
    },
    {
      key: 'amount',
      sortable: true,
      label: 'Kwota'
    },
    {
      key: 'date',
      sortable: true,
      label: 'Data'
    }
  ]

  created() {
   this.getUsers()
  }

  public addFilter() {
    if(!this.allSelectedFilters.includes(this.selectedFilter)){
      this.getData();
      this.allSelectedFilters.push(this.selectedFilter);
      let dataFilteredByCat :Array<Expense> =[];
      let dataFilteredByMon :Array<Expense> =[];
      let filteredData :Array<Expense> =[];
      console.log(this.expenses)
      for(let elem of this.allSelectedFilters) {
        dataFilteredByCat=dataFilteredByCat.concat(this.expenses.filter(expense => expense.category == elem))
        dataFilteredByMon=dataFilteredByMon.concat(this.expenses.filter(expense => this.month(expense.date) == elem))
      }
      if(dataFilteredByCat.length>0 && dataFilteredByMon.length>0) {
        for(let expense of dataFilteredByCat) {
          for(let exp of dataFilteredByMon) {
            if(expense.id===exp.id) {
              filteredData.push(expense);
            }
          }
        }
      }
      else if (dataFilteredByCat.length==0 && dataFilteredByMon.length>0) {
        filteredData.push(...dataFilteredByMon)
      }
      else if (dataFilteredByCat.length>0 && dataFilteredByMon.length==0) {
        filteredData.push(...dataFilteredByCat)
      }
      this.expenses=filteredData;
      console.log(filteredData)
    }
    this.selectedFilter="";
  }
  public month = (date: string) => {
    let month = date;
    month=month.slice(5,-3)
    if(month[0]==="0") {
        month=month[1]
    }
    month=this.months[+month-1]
    return month;
  }
  public removeFilters() {
    this.allSelectedFilters=[]
    this.getData();
  }
  public async getUsers() :Promise<void> {
    this.users =[];
    let rootRef = firebase.database().ref('/' + this.selected);
    let vm = this;
    rootRef.orderByKey().on("value", function(snapshot:any) {
    vm.users=[]
    snapshot.forEach(function(data:any):any {
        vm.users.push(data.key);
      });
    });
  }

  public onRowSelected(items :Array<Expense>) {
    this.selectedExpenses = items
  }

  public getData() :void {
    this.expenses=[];
    let rootRef = firebase.database().ref('/' + this.selected);
    let vm = this;
    rootRef.on('value', function(snapshot) {
      vm.expenses = []
      snapshot.forEach(function(data :any) {
        vm.expenses.unshift(data.val());
      });            
    });
    console.log(this.expenses)
  }
  public removeExpense() :void {
    if(this.selectedExpenses.length>0) {
      let rootRef = firebase.database().ref('/' + this.selected);
      for (let elem of this.expenses) {
        for(let el of this.selectedExpenses) {
          if(elem.id==el.id) {
            rootRef.child(elem.id).remove();
          }
          } 
        }
      this.expenses = [];
      this.getData(); 
    }
  }
  public copyExpense() {
    this.selectedExpenses[0].user=this.selected;
    this.$emit('changeToForm');
    this.$store.dispatch("setExpenseToCopy", this.selectedExpenses[0]);
  }
}
