




















































import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
declare function require(name:string): any;
import firebase from 'firebase';
require('firebase/database');
import VueTypeaheadBootstrap from 'vue-typeahead-bootstrap';
import 'bootstrap/scss/bootstrap.scss';

type Expense = {
  user:string,
  category:string,
  description:string,
  date:string,
  amount: string,
  id:string,
}
@Component({
  components: {
    VueTypeaheadBootstrap
  }
})
export default class Form extends Vue {
  private usersSet: any = new Set()
  private users :Array<string> = []
  private categories: Array<string> = ["Żywność","Kredyt","Środki czystości", "Zwierzęta", "Rozrywka", "Rachunki", "Wyposażenie", "Inne"]
  private expense: Expense = {
    user:"",
    category:"",
    description:"",
    date:"",
    amount: "",
    id:"",
  }
  created() {
    this.getUsers();
  }
  get rootRef(): any{
    return firebase.database().ref('/'+ this.capitalize(this.expense.user))
  }
  get expenseToCopy(): Expense{
    return this.$store.getters.getExpenseToCopy;
  }
  @Watch('expenseToCopy')
  copyExpense(newVal: Expense) {
    this.expense = newVal;
  }
  public addExpense(): void {
    if(this.expense.user=="" || this.expense.category=="" || this.expense.date=="" || this.expense.description==""  ||this.expense.amount=="") {
      alert("aby dodać wydatek wypełnij wszystkie pola!")
    }
    else {
      const autoId = this.rootRef.push().key
      this.rootRef.child(autoId).set({
        category: this.expense.category,
        description: this.capitalize(this.expense.description),
        date: this.expense.date,
        amount: this.expense.amount,
        id: autoId,
      })
      this.updateUsers();
      this.clearForm();
    }
  }
  public clearForm(): void {
    this.expense.user="";
    this.expense.category="";
    this.expense.description="";
    this.expense.date="";
    this.expense.amount= "";
  }
  public async getUsers(): Promise<void> {
    let vm=this
    await this.rootRef.orderByKey().on("value", function(snapshot:any) {
      snapshot.forEach(function(data:any):any {
        vm.users.push(data.key);
      });
    }); 
  }
  public updateUsers(): void {
    this.usersSet.add(this.expense.user);
  }
  public capitalize (s: string) :string {
    return s.charAt(0).toUpperCase() + s.slice(1)
  }
}
